<template>
	<div v-show="visible" class="newt">
		<input class="newt__input" ref="new" v-model.trim="text">
		<CommandTips/>
	</div>
</template>

<script>
import {EventBus} from '@/eventBus'
import {Storage} from '@/storage'
import CommandTips from '@/components/CommandTips'
export default {
	props: {
		visible: Boolean
	},
	components: {CommandTips},
	data() {
		return {
			text: ''
		}
	},
	methods: {
		saveTask() {
			if (!this.text) return
			Storage.saveTask({text: this.text})
			this.text = ''
			this.$emit('update')
		},
		abortTask() {
			this.text = ''
		}
	},
	mounted() {
		EventBus.$on('save-task', () => this.saveTask());
		EventBus.$on('abort-task', () => this.abortTask());
	}
}
</script>

<style>
.newt {
	padding-bottom: 10px;
}
.newt__input {
    font-size: 20px;
    color: black;
    background-color: var(--grey);
    border: 3px solid black;
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
}
</style>
